<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 原因代码 -->
            <a-form-model-item
              label="原因代码"
              prop="reasonCode"
              ref="reasonCode"
            >
              <a-input
                v-model="entity.reasonCode"
                placeholder="请输入原因代码"
                name="reasonCode"
                :disabled="!!id"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 原因名称 -->
            <a-form-model-item
              label="原因名称"
              prop="reasonName"
              ref="reasonName"
            >
              <a-input
                v-model="entity.reasonName"
                placeholder="请输入原因名称"
                name="reasonName"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 保养分类 -->
            <a-form-model-item
              label="保养分类"
              prop="category"
              ref="category"
            >
              <a-select
                v-model="entity.category"
                placeholder="请选择保养分类"
                name="category"
              >
                <a-select-option v-for="item in categoryOptions" :key="item.value" :value="item.value">
                  {{ item.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 优先级 -->
            <a-form-model-item
              label="优先级"
              prop="priority"
              ref="priority"
            >
              <a-select
                v-model="entity.priority"
                placeholder="请选择优先级"
                name="priority"
              >
                <a-select-option v-for="item in priorityOptions" :key="item.value" :value="item.value">
                  {{ item.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="18">
            <!-- 描述 -->
            <a-form-model-item
              label="描述"
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :maxLength="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 保养方法 -->
            <a-form-model-item
              label="保养方法"
              prop="maintenanceMethod"
              ref="maintenanceMethod"
            >
              <a-textarea
                v-model="entity.maintenanceMethod"
                placeholder="请输入保养方法"
                :rows="6"
                :maxLength="2000"
                name="maintenanceMethod"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="适用设备" v-if="false">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-form-model-item label="">
                <a-table
                  :columns="deviceTypeColumns"
                  :data-source="entity.deviceTypes || []"
                  :pagination="false"
                  size="small"
                  bordered
                >
                  <template slot="action" slot-scope="text, record, index">
                    <a-button type="link" @click="removeDeviceType(index)" danger>
                      <a-icon type="delete" /> 移除
                    </a-button>
                  </template>
                </a-table>
                <div style="margin-top: 10px;">
                  <a-button type="dashed" @click="showAddDeviceType" style="width: 100%">
                    <a-icon type="plus" /> 添加适用设备类型
                  </a-button>
                </div>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
      <g-collapse headerName="详细信息" v-if="id">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditMaintenanceReason",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        reasonCode: "",
        reasonName: "",
        category: undefined,
        priority: undefined,
        status: "启用",
        description: "",
        maintenanceMethod: "",
        deviceTypes: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      // 保养分类选项
      categoryOptions: [
        { value: "定期保养", label: "定期保养" },
        { value: "预防性保养", label: "预防性保养" },
        { value: "故障预警保养", label: "故障预警保养" },
        { value: "使用寿命保养", label: "使用寿命保养" },
        { value: "环境影响保养", label: "环境影响保养" }
      ],
      // 优先级选项
      priorityOptions: [
        { value: "高", label: "高" },
        { value: "中", label: "中" },
        { value: "低", label: "低" }
      ],
      // 设备类型列
      deviceTypeColumns: [
        {
          title: "设备类型名称",
          dataIndex: "name",
          key: "name",
          width: "40%"
        },
        {
          title: "设备家族",
          dataIndex: "familyName",
          key: "familyName",
          width: "20%"
        },
        {
          title: "制造商",
          dataIndex: "manufacturer",
          key: "manufacturer",
          width: "20%"
        },
        {
          title: "操作",
          dataIndex: "action",
          key: "action",
          width: "20%",
          scopedSlots: { customRender: "action" }
        }
      ],
      rules: {
        reasonCode: [
          { required: true, message: "原因代码不能为空", trigger: "change" },
          {
            pattern: /^M[0-9]{3}$/,
            message: "原因代码格式不正确，应为M开头加3位数字",
            trigger: "change"
          }
        ],
        reasonName: [
          { required: true, message: "原因名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            message: "原因名称不能包含前后空格",
            trigger: "change"
          }
        ],
        category: [
          { required: true, message: "保养分类不能为空", trigger: "change" }
        ],
        priority: [
          { required: true, message: "优先级不能为空", trigger: "change" }
        ],
        status: [
          { required: true, message: "状态不能为空", trigger: "change" }
        ],
        maintenanceMethod: [
          { required: true, message: "保养方法不能为空", trigger: "change" }
        ]
      }
    };
  },

  methods: {
    // 显示添加设备类型弹窗（预留功能）
    showAddDeviceType() {
      // 这里可以实现添加适用设备类型的弹窗
      console.log('添加适用设备类型');
    },
    
    // 移除设备类型
    removeDeviceType(index) {
      this.entity.deviceTypes.splice(index, 1);
    },
    
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取保养原因数据
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            reasonCode: "M001",
            reasonName: "定期保养1",
            category: "定期保养",
            priority: "高",
            status: "启用",
            description: "部件磨损导致的定期保养需求，需及时处理",
            maintenanceMethod: "建议进行彻底保养，包含硬件检查和软件升级。\n\n1. 检查所有硬件接口\n2. 清理内部灰尘\n3. 更新固件\n4. 校准传感器\n5. 测试运行状态",
            deviceTypes: [
              { id: 1, name: "华为 S5820-32C-EI 交换机", familyName: "交换机", manufacturer: "华为" },
              { id: 4, name: "思科 ASR 9000 路由器", familyName: "路由器", manufacturer: "思科" }
            ],
            createdBy: "admin",
            createdTime: "2024-03-18 10:00:00",
            modifiedBy: "admin",
            modifiedTime: "2024-03-18 11:55"
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },

    resetFormData() {
      this.entity = {
        reasonCode: "",
        reasonName: "",
        category: undefined,
        priority: undefined,
        status: "启用",
        description: "",
        maintenanceMethod: "",
        deviceTypes: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },

    async save() {
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.reasonName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null
          });
        }, 500);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}
</style> 